export default {
	name: "sunui-star",
	props: {
		iconName: {
			type: String,
			default: 'icon-star'
		},
		isTips: {
			type: Boolean,
			default: false
		},
		starSize: {
			type: String,
			default: '1.5em'
		},
		maxStar: {
			type: Number,
			default: 5
		},
		defaultStar: {
			type: Number,
			default: 3
		},
		disabledStar: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			curStarNum: 0
		}
	},
	created() {
		console.log(this.iconName);
		this.curStarNum = this.defaultStar;
	},
	methods: {
		changeStar(e) {
			if (this.disabledStar) {
				return;
			}
			this.curStarNum = Number(e.currentTarget.dataset.value) + 1;
			this.$emit("changeStar", {
				curStar: this.curStarNum
			});
			if (this.isTips) {
				uni.showToast({
					title: `${this.curStarNum}é¢—`,
					icon: 'none'
				})
			}
		}
	}
}
